@import url(https://fonts.googleapis.com/css?family=Lacquer|Shadows+Into+Light&display=swap);
.header {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Shadows Into Light', cursive;
}
.header-title {
  position: absolute;
  bottom: -10%;
  right: 19%;
  color: #ffcb05;
  font-size: 25px;
  -webkit-transform: rotateZ(6deg);
          transform: rotateZ(6deg);
}

.header-logo img {
  height: 15vh;
  width: auto;
}

.header-logo {
  height: 15vh;
  width: auto;
  margin: 2vh 0;
  position: relative;
}

.shop-header a {
  color: rgb(255, 255, 255);
  text-decoration: none;
}
.shop-header a:hover {
  color: rgba(185, 185, 185, 0.411);
}

.shop-header .logo {
  font-size: 4rem;
  padding-left: 1rem;
}

.shop-header .shopping-cart {
  align-self: center;
  font-size: 1.3rem;
  padding-right: 1rem;
  text-align: center;
}

.shop-header .shopping-cart .cart-icon {
  font-size: 2.2rem;
  margin-right: 10px;
}

@media (max-width: 1000px) { 

}
@media (max-width: 667px) { 
  .header-logo {
    height: 14ch;
    width: auto;
  }
  .header-logo img {
    height: 14ch;
  }
  .header-title {
    font-size: 13px;
  }
}
.home-page{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    font-family: 'Shadows Into Light', cursive;
    margin-top: 5vh;
    background: rgba(255, 255, 255, 0.575);
    height: 100%;
}
.link-btn {
    margin: 5vh 2vw;
    width: 30vh;
    height: 30vh;
    /* border-radius: 10px; */
    display: flex;
    justify-content: center;
    align-items: flex-start;

}

.browse {
    background: url(/Pokemon-App/static/media/browse.164eef80.jpg) center no-repeat / cover;
}
.search {
    background: url(/Pokemon-App/static/media/search.0d2a7241.jpg) center no-repeat / cover;
}
.mini-game {
    background: url(/Pokemon-App/static/media/mini-game.9a1a7f89.jpg) center no-repeat / cover;
}
.link-btn span {
    font-size: 43px;
    color: rgb(27, 7, 7);
    background: rgba(255, 255, 255, 0.8);
    border-radius: 10px 10px 0 0;
    width: 100%;
    text-align: center;
    /* border: 1px solid rgb(27, 7, 7); */
}

