.preview-item-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: rgb(18, 41, 70);
  background: rgba(255, 255, 255, 0.575);
  margin-bottom: 3vh; 
}
.preview-item {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-left: 7vh;
}
.item-details-images img {
  width: auto;
  height: 15vh;
  color: #000;
  margin: -.6vh;
  -webkit-filter: brightness(100%);
          filter: brightness(100%);
}
.item-details-images {
  position: relative;
  width: auto;
  height: 15vh;
}

.item-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-left: 2vh;
}

.preview-item-box h4 {
  font-size: 50px;
}

.item-details-stats {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin: 2vh 0;
}
.item-details-stats .stats-bar{
  height: 2vh;
  width: 25vh;
  border: 1px solid rgb(18, 41, 70);
}
.item-details-stats .stats-bar .stats-bar-progress{
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.base-exp {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-weight: 400;
  font-size: .5em;
  text-transform: uppercase;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: -5%;
  right: -5%;
  border: 1px solid #000;
  border-radius: 50%;
  height: 6ch;
  width: 6ch;
}

@media (max-width: 1000px) { 
  .preview-item {
    margin: 0;
  }
  .item-details-images img {
    margin: 0;
  }
}
@media (max-width: 667px) { 
  .preview-item {
    flex-direction: column;
  }
}


.pokemon-species {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
}
.species-category {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;
    margin: 2vh;
}
.pokemon-evolutions-chain {
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    color: black;
    margin: 2vh 0;
}
.pokemon-evolutions {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
}
.pokemon-evolution {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 0 2vh;
    position: relative;
}
.fa-long-arrow-alt-right {
    position: absolute;
    top: 40%;
    left: -25%;
}
.second-evolutions-chain {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
}
.item-list {
  list-style: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  background: rgba(255, 255, 255, 0.575);
  padding: 0;
}

.show-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 135px;
  height: 120px;
  margin: 1.5vh;
  /* border: 1px solid #fff; */
}
.show-item img {
  width: 90px;
  height: 90px;
}

.show-item span{
  font-size: 15px;
  font-weight: 500;
  color: #000;
}

/* .show-item:hover{
  text-decoration: black;
} */

.pagination {
  cursor: pointer;
  display: flex;
  justify-content: center;
  margin: 1vh 0;
}
.pokemon-search-page {
    background: rgba(255, 255, 255, 0.575);
    display: flex;
    align-items: center;
    flex-direction: column;
}
.pokemon-search-list{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 1vh 2vh;
}
.pokemon-search-list a {
    margin: .5vh;
}
body {
    background: url(/Pokemon-App/static/media/bg1.26b5551b.png) no-repeat fixed center / cover;
    color: #fff;
    margin: 0;
    padding: 0;
    font-family: 'Lacquer', sans-serif;
}
